import React, { Component, Fragment,useEffect,useState }  from 'react';
import { useSelector,useDispatch } from 'react-redux';
import { Link,useHistory } from 'react-router-dom';
import { setDatabox, setEvsdata, updateAlert } from '../../store/ssoSlice';
import { helperData } from '../../store/utils/helperData';
import { fetchEvsData, fetchEvsUpdate } from '../../store/utils/ssoApi';
import EVSVote from '../apps/evs/EVSVote';
import './RoleEVS.css';
import moment from 'moment';
import { FcStatistics,FcAssistant,FcComboChart,FcRatings } from 'react-icons/fc';
import EVSMonitor from '../apps/evs/EVSMonitor';
import EVSStrongroom from '../apps/evs/EVSStrongroom';
import EVSResult from '../apps/evs/EVSResult';
import Live from '../../assets/img/live.gif';
import EVSVoters from '../apps/evs/EVSVoters';
import EVSReceipt from '../apps/evs/EVSReceipt';


const RoleEVS = ({data}) => {

	  const { REACT_APP_API_URL : API_URL } = process.env; 
	  const { sso }  = useSelector(state => state);
	  const { user } = sso;
	  const dispatch = useDispatch();
	  //const modules = user.roles.find(r => r.app_tag == 'evs').data;
	  const [ page,setPage ] = useState(null)
	  const [ roles, setRoles ] = useState([])
	  const [ modules, setModules ] = useState([])
      console.log(modules)

	  const voteNow = async ({id},tag) => {
        const resp = await fetchEvsData(id,tag);
		if(resp.success){
			const info = `
			  WELCOME TO ELECTA VOTING SYSTEM !! \n
			  Please click on the 'CHOOSE' button to make a choice of candidate or opinion.\n
			  Submit your all your choices by clicking on the 'SUBMIT FINAL VOTE' button above to cast your vote, THANK YOU!
			`
			alert(info)
			dispatch(setEvsdata({ ...resp.data,allowAccess:true,id }))
			setPage('votenow')
			//setTimeout(dispatch(updateAlert({show:true,message:`WELCOME!, PLEASE CLICK ON 'CHOOSE' TO MAKE A PICK AND SUBMIT FINALLY!`,type:'info'})),2000)
		}
      }

	  const resultNow = async ({id},tag) => {
        const resp = await fetchEvsData(id,tag);
		if(resp.success){
			const info = `
			  WELCOME TO ELECTA VOTING SYSTEM !! \n
			 The Election has ended and the Results are presented on the upcoming page.\n
			`
			alert(info)
			dispatch(setEvsdata({ ...resp.data,allowAccess:true,id }))
			setPage('result')
			//setTimeout(dispatch(updateAlert({show:true,message:`WELCOME!, PLEASE CLICK ON 'CHOOSE' TO MAKE A PICK AND SUBMIT FINALLY!`,type:'info'})),2000)
		}
      }

	  const monitorNow = async ({id},tag) => {
        const resp = await fetchEvsData(id,tag);
		if(resp.success){
			dispatch(setEvsdata({ ...resp.data,allowAccess:true,id}))
			setPage('monitor')
		}
      }

	  const followNow = async ({id},tag) => {
        const resp = await fetchEvsData(id,tag);
		if(resp.success){
			dispatch(setEvsdata({ ...resp.data,allowAccess:true,id}))
			setPage('strongroom')
		}
      }

	  const viewReceipt = async ({id},tag) => {
        const resp = await fetchEvsData(id,tag);
		if(resp.success){
			const info = `
			  WELCOME TO ELECTA VOTING SYSTEM !! \n
			 You successfully casted your vote, Find the confirmation on this Page.\n
			`
			alert(info)
			dispatch(setEvsdata({ ...resp.data,allowAccess:true,id }))
			setPage('receipt')
			//setTimeout(dispatch(updateAlert({show:true,message:`WELCOME!, PLEASE CLICK ON 'CHOOSE' TO MAKE A PICK AND SUBMIT FINALLY!`,type:'info'})),2000)
		}
      }

	  const getUpdates = async (tag) => {
		if(tag){
		   const resp = await fetchEvsUpdate(tag);
		   if(resp.success){
              const dm = resp.data.find(r => r.app_tag == 'evs');
			  if(dm) setModules(dm.data)
		   }else{
			  const dm = user.roles.find(r => r.app_tag == 'evs');
			  if(dm) setModules(dm.data)
		   }
		}
	  }


	  useEffect(() => {
		 getUpdates(user && user.user && user.user.tag)
	  },[])

	  const MainPage = () => (
		<>
		  {modules && modules.map((mod,i) =>
            
			<div className="col-md-3" key={i}>
			  <div className="token-info nitro">
			    <img className={`fas fa-2x text-info`} style={{ height:'100px'}} src={`${API_URL}/photos/evs/?tag=logo&eid=${mod.id}`}/>
				<div className="gaps-2x" />
				<div><Link title={mod.name} className="btn btn-primary" style={{color:'#f8e0cd'}}><b>{mod.tag.toUpperCase()} ELECTIONS</b></Link></div>
				<small className="my-2 mx-3 center"><b>{mod.name && mod.name.toUpperCase()}</b></small>
				
				{/* Election Shortcuts */}
				<div className="evs__icons">
					{/*<span className="monitor"><FcAssistant className="monitor_icon"/></span>*/}
					{ (mod.allow_monitor == 1 && mod.status == 'STARTED') && <span className="monitor"  title="Public Monitor" onClick={()=> monitorNow(mod,(user && user.user.tag))}><FcStatistics className="monitor_icon"/></span>}
					{ (mod.allow_vip == 1 && ['STARTED','STAGED','HALTED'].includes(mod.status)) && <span className="monitor"  title="Strongroom Monitor" onClick={()=> followNow(mod,(user && user.user.tag))}><FcComboChart className="monitor_icon"/></span> }
					{ /*(mod.allow_result == 1 && mod.status == 'ENDED') && */<span className="monitor" title="Final Results" onClick={()=> resultNow(mod,(user && user.user.tag))}><FcRatings className="monitor_icon"/></span>}
					{ /*['ENDED'].includes(mod.status) && <span className="monitor"><FcStatistics className="monitor_icon"/></span>*/} { /* JSON.stringify(mod) */ }
					{ (['STARTED'].includes(mod.status) && (mod.vote_status == 0 || mod.vote_status == null) && mod.voter != null) && <span className="vote_cover" onClick={()=> voteNow(mod,(user && user.user.tag))}>Vote</span> }
					{ (['STARTED'].includes(mod.status) && mod.vote_status == 1 && mod.voter != null) && <span className="vote_cover" onClick={()=> viewReceipt(mod,(user && user.user.tag))}>Ballot</span> }
				</div>

				<div className="evs__icons" style={{ marginTop:'-40px'}}>
					<hr/>
					{ (['STARTED'].includes(mod.status) && (mod.vote_status == 0 || mod.vote_status == null) && mod.voter != null) && <span className="" onClick={()=> voteNow(mod,(user && user.user.tag))}>Admin Dashboard</span> }
				</div>


				<div className="evs__info">
					<span><b>Status:</b> { mod.status == 'STARTED' ? <img src={Live} className="" /> : mod.status }</span>
					{ mod.vote_time && <span><b>Voted:</b> {moment(mod.vote_time).format('LLL')}</span>}
					{ mod.start && <span><b>Opens:</b> {moment(mod.start).format('LLL')}</span> }
					{ mod.end && <span><b>Closes:</b> {moment(mod.end).format('LLL')}</span> }
					{<span><b>VOTERS:</b> { mod.voters_count }</span> }
					{false && <span><b>TURNOUT:</b> { mod.voters_count }</span> }
				</div>
				


				{ mod.on_test == 1 &&
				<div className="trials">
					<h3>ELECTION TRIALS</h3>
					<p>Please note that the current processes are trials only, Actual election date shall be communicated soon!</p>
				</div>
                }

				
              </div>
			</div> || <h1 key={i}>NO ACTIVE ELECTIONS STAGED !</h1>

			)}
		</>
	   )

	   const PageRenderer = () => {
		   switch(page){
			   case 'votenow': return <EVSVote setPage={setPage}/>; break;
			   case 'monitor': return <EVSMonitor setPage={setPage}/>; break;
			   case 'strongroom': return <EVSStrongroom setPage={setPage}/>; break;
			   case 'result': return <EVSResult setPage={setPage}/>; break;
			   case 'list': return <EVSVoters setPage={setPage}/>; break;
			   case 'receipt': return <EVSReceipt setPage={setPage}/>; break;
			   default: return <MainPage setPage={setPage}/>;
		   }
	   }
	   return <PageRenderer/>
}

export default RoleEVS;
